@import "utils";
@import "fonts";
@import "typography";

@font-face {
  font-family: Avenir; /* Имя шрифта */
  src: url(../fonts/Avenir/Avenir-Book.ttf); /* Путь к файлу со шрифтом */
}

%main_fontsize_small {
  font-size: 14px;
  line-height: 16px;
}

%main_fontsize_medium {
  font-size: 16px;
  line-height: 19px;
}

%main_fontsize_large {
  font-size: 20px;
  line-height: 23px;
}

%main_fontsize_xl {
  font-size: 28px;
  line-height: 33px;
}

$header_small_img_size: 30px;
$main_review_photo_size: 50px;

$grey-title: #3d4e61;
$light-grey-title: #687c94;
$light-green-title: #118488;
$light-grey-border: #e1e7ed;
$red-book-btn: #fd5c63;

@mixin small_img_block($small_img_size) {
  width: $small_img_size;
  height: $small_img_size;
}

@mixin flex_row_between {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
}

@mixin align_items_center {
  justify-items: center;
  align-items: center;
}

body {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  font-family: Avenir, sans-serif;
  color: $grey-title;
}

h1 {
  font-size: 36px;
  line-height: 42px;
  color: $grey-title;
  margin-top: 0;
}

h2 {
  @extend %main_fontsize_large;
  color: $grey-title;
}

a {
  text-decoration: none;
  color: $grey-title;
}

.is-bold {
  font-weight: bold;
}

.review {
  display: flex;
  width: 491px;
  flex-direction: row;
  flex-wrap: wrap;
}

.review_details {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.review_details_photo_wrapper {
  display: flex;
  margin-right: 21px;
}

.review_photo {
  width: $main_review_photo_size;
  height: $main_review_photo_size;
}

.details_name_date {
  width: 173px;
  height: 28px;
  margin: 2px;
}

.details_name {
  @extend %main_fontsize_medium;
  font-weight: bolder;
}

.details_date {
  @extend %main_fontsize_large;
  font-weight: 500;
}

.descriptive_text {
  @extend %main_fontsize_medium;
  width: 491px;
  height: 120px;
  color: $grey-title;
}

.header-container {
  height: 60px;
  background: white;
  @include flex_row_between;
  @include align_items_center;

  .header-left {
    padding-left: 26px;

    .header-small-logo-img {
      @include small_img_block($header_small_img_size);
    }
  }

  .header-right {
    margin-right: 19px;
    @include flex_row_between;

    .header-nav-wrapper {
      @include flex_row_between;
      @include align_items_center;

      .nav-wrap {
        .nav-item {
          list-style: none;
          text-decoration: none;
          display: inline;

          a {
            font-size: 16px;
            line-height: 22px;
            color: $grey-title;
            text-decoration: none;
            margin-right: 30px;
          }
        }
      }
    }

    .header-profile-wrapper {
      display: flex;
      justify-content: center;
      align-content: center;

      .header-logo {
        @include small_img_block($header_small_img_size);
        border-radius: 50%;
      }
    }
  }
}

.main_photo {
  width: 100%;
  height: 464px;
  object-fit: cover;
}

button.contact-host {
  border: 1px solid $light-green-title;
  box-sizing: border-box;
  border-radius: 4px;
  width: 150px;
  height: 50px;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  color: $light-green-title;
  margin-bottom: 75px;
  position: absolute;
  left: 0;
  bottom: 0;
}

.contact-host {
  margin-top: 5px;
  margin-bottom: 30px;
  @extend %main_fontsize_medium;
}

