%footer_column_link {
  text-decoration: none;
  color: $grey-title;
}

.footer-column-item-title {
  @extend %footer_column_link;
}

.footer-column-title {
  @extend %footer_column_link;
  font-weight: bold;
}

.footer-item {
  margin-top: 10px;
}

footer {
  background: white;
  box-shadow: 0 -1px 0 $light-grey-border;
  display: flex;

  .footer-container {
    margin: 44px auto;
    display: grid;
    width: 901px;
    grid-template-columns: repeat(3, auto);
    font-size: 16px;
    line-height: 19px;
    color: $grey-title;
  }
}
